import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from './components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './components/ui/tabs';
import { Button } from './components/ui/button';
import { Badge } from './components/ui/badge';
import { supabase } from './lib/supabaseClient';

interface DailyStats {
  streak: number;
  todayProblems: number;
  monthlyProblems: number;
  averageRating: number;
  dailyLimit: number;
}

interface PlanInfo {
  currentPlan: string;
  monthlyFee: number;
  yearlyFee: number;
  features: string[];
  nextBilling: string;
}

interface UserAccount {
  email: string;
  registeredAt: string;
}

export default function MyPage({ onBackToMenu, onStartTraining, onShowAuth }: { 
  onBackToMenu: () => void;
  onStartTraining: (problem: string) => void;
  onShowAuth?: () => void;
}) {
  // Record current page as my page for refresh persistence
  useEffect(() => {
    localStorage.setItem('englishTrainingCurrentPage', 'mypage');
  }, []);

  const [dailyStats, setDailyStats] = useState<DailyStats>({
    streak: 0,
    todayProblems: 0,
    monthlyProblems: 0,
    averageRating: 0,
    dailyLimit: 100
  });
  const [planInfo, setPlanInfo] = useState<PlanInfo>({
    currentPlan: '',
    monthlyFee: 980,
    yearlyFee: 9800,
    features: [],
    nextBilling: '2025/08/13'
  });
  const [userAccount, setUserAccount] = useState<UserAccount>({
    email: '',
    registeredAt: ''
  });
  const [isLoading, setIsLoading] = useState(true);
  const [bookmarks, setBookmarks] = useState<string[]>([]);
  const [reviewList, setReviewList] = useState<any[]>([]);
  const [retryList, setRetryList] = useState<any[]>([]);
  const [reviewLoading, setReviewLoading] = useState(false);

  useEffect(() => {
    loadUserData();
    loadBookmarks();
    loadReviewData();
  }, []);

  const loadReviewData = async () => {
    setReviewLoading(true);
    try {
      const { data } = await supabase.auth.getSession();
      const token = data.session?.access_token;
      
      const headers: HeadersInit = {
        'Content-Type': 'application/json',
      };
      
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      const [reviewResponse, retryResponse] = await Promise.all([
        fetch('/api/review-list', { headers }),
        fetch('/api/retry-list', { headers })
      ]);
      
      if (reviewResponse.ok) {
        const reviewData = await reviewResponse.json();
        setReviewList(reviewData || []);
      } else {
        console.warn('Failed to load review list:', reviewResponse.status);
        setReviewList([]);
      }
      
      if (retryResponse.ok) {
        const retryData = await retryResponse.json();
        setRetryList(retryData || []);
      } else {
        console.warn('Failed to load retry list:', retryResponse.status);
        setRetryList([]);
      }
    } catch (error) {
      console.error('Failed to load review data:', error);
      setReviewList([]);
      setRetryList([]);
    } finally {
      setReviewLoading(false);
    }
  };

  const loadBookmarks = () => {
    const savedBookmarks = localStorage.getItem('englishTrainingBookmarks');
    if (savedBookmarks) {
      try {
        const bookmarksArray = JSON.parse(savedBookmarks);
        setBookmarks(bookmarksArray);
      } catch (error) {
        console.error('Failed to load bookmarks:', error);
      }
    }
  };

  const removeBookmark = (bookmarkToRemove: string) => {
    const updatedBookmarks = bookmarks.filter(bookmark => bookmark !== bookmarkToRemove);
    setBookmarks(updatedBookmarks);
    localStorage.setItem('englishTrainingBookmarks', JSON.stringify(updatedBookmarks));
  };

  const loadUserData = async () => {
    try {
      setIsLoading(true);
      
      // Get auth token for API requests
      const { data } = await supabase.auth.getSession();
      const token = data.session?.access_token;
      
      const headers: HeadersInit = {
        'Content-Type': 'application/json',
      };
      
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      // Fetch real progress data from API
      const progressResponse = await fetch('/api/progress-report', { headers });
      if (progressResponse.ok) {
        const progressData = await progressResponse.json();
        setDailyStats({
          streak: progressData.streak || 0,
          monthlyProblems: progressData.monthlyProblems || 0,
          averageRating: parseFloat(progressData.averageRating) || 0,
          todayProblems: progressData.todayProblems || 0,
          dailyLimit: progressData.dailyLimit || 50
        });
      } else {
        console.warn('Failed to load progress data, using defaults');
        setDailyStats({
          streak: 0,
          monthlyProblems: 0,
          averageRating: 0,
          todayProblems: 0,
          dailyLimit: 50
        });
      }

      // Update plan information based on membership
      const membershipType = progressData?.membershipType || 'standard';
      setPlanInfo({
        currentPlan: membershipType === 'premium' ? '' : '',
        monthlyFee: membershipType === 'premium' ? 1980 : 980,
        yearlyFee: membershipType === 'premium' ? 19800 : 9800,
        features: membershipType === 'premium' ? [
          ' ',
          '1 100',
          ' ',
          ' ',
          ' ',
          ' ',
          ' '
        ] : [
          ' 100',
          '1 50',
          ' ',
          ' ',
          ' ',
          ' ',
          ' '
        ],
        nextBilling: '2025/08/13'
      });

      // Mock user account
      setUserAccount({
        email: 'slazengersnow@gmail.com',
        registeredAt: ''
      });

    } catch (error) {
      console.error('Failed to load user data:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const cancelSubscription = () => {
    alert('');
  };

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center">
        <div className="bg-white rounded-lg shadow-lg p-8 text-center">
          <div className="animate-spin w-8 h-8 border-4 border-blue-500 border-t-transparent rounded-full mx-auto mb-4"></div>
          <p className="text-gray-600">...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <div className="flex justify-between items-center mb-6">
          <div className="flex items-center space-x-3">
            <button 
              onClick={onBackToMenu}
              className="text-gray-500 hover:text-gray-700"
            >
              
            </button>
            <div className="w-8 h-8 bg-blue-500 text-white rounded-full flex items-center justify-center">
              <span className="text-sm">A</span>
            </div>
            <h1 className="text-xl font-bold text-gray-900"></h1>
          </div>
          <button 
            onClick={onBackToMenu}
            className="flex items-center space-x-1 text-gray-500 hover:text-gray-700"
          >
            <span></span>
            <span></span>
          </button>
        </div>

        <Tabs defaultValue="progress" className="space-y-6">
          <TabsList className="grid w-full grid-cols-5 bg-white rounded-lg p-1">
            <TabsTrigger value="progress" className="text-sm"></TabsTrigger>
            <TabsTrigger value="bookmarks" className="text-sm"></TabsTrigger>
            <TabsTrigger value="practice" className="text-sm"></TabsTrigger>
            <TabsTrigger value="simulation" className="text-sm"></TabsTrigger>
            <TabsTrigger value="account" className="text-sm"></TabsTrigger>
          </TabsList>

          {/* Progress Report Tab */}
          <TabsContent value="progress">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
              {/* Streak Days */}
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm text-gray-600 mb-1"></div>
                  <div className="text-3xl font-bold text-green-600">{dailyStats.streak}</div>
                  <div className="text-xs text-gray-500"></div>
                </CardContent>
              </Card>

              {/* Monthly Problems */}
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm text-gray-600 mb-1"></div>
                  <div className="text-3xl font-bold text-blue-600">{dailyStats.monthlyProblems}</div>
                  <div className="text-xs text-gray-500"></div>
                </CardContent>
              </Card>

              {/* Average Rating */}
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm text-gray-600 mb-1"></div>
                  <div className="text-3xl font-bold text-yellow-600">{dailyStats.averageRating}</div>
                  <div className="text-xs text-gray-500"></div>
                </CardContent>
              </Card>

              {/* Today Problems */}
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm text-gray-600 mb-1"></div>
                  <div className="text-3xl font-bold text-orange-600">{dailyStats.todayProblems}/{dailyStats.dailyLimit}</div>
                  <div className="text-xs text-gray-500"> {dailyStats.dailyLimit - dailyStats.todayProblems}</div>
                </CardContent>
              </Card>
            </div>


          </TabsContent>

          {/* Repetitive Practice Tab */}
          <TabsContent value="practice">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Review List (2) */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center space-x-2">
                    <span className="text-red-500"></span>
                    <span> (2)</span>
                  </CardTitle>
                  <p className="text-sm text-gray-600"></p>
                </CardHeader>
                <CardContent>
                  {reviewLoading ? (
                    <div className="flex justify-center py-8">
                      <div className="animate-spin w-6 h-6 border-2 border-red-500 border-t-transparent rounded-full"></div>
                    </div>
                  ) : reviewList.length === 0 ? (
                    <div className="text-center py-8 text-gray-500">
                      <div className="text-4xl mb-2"></div>
                      <p></p>
                      <p className="text-sm"></p>
                    </div>
                  ) : (
                    <div className="space-y-2">
                      {reviewList.slice(0, 10).map((item, index) => (
                        <div 
                          key={item.id || index} 
                          className="bg-red-50 border border-red-200 rounded-lg p-3 hover:bg-red-100 cursor-pointer transition-colors"
                          onClick={() => onStartTraining(item.japaneseSentence)}
                        >
                          <div className="text-gray-800 font-medium">{item.japaneseSentence}</div>
                        </div>
                      ))}
                    </div>
                  )}
                </CardContent>
              </Card>

              {/* Retry List (3) */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center space-x-2">
                    <span className="text-orange-500"></span>
                    <span> (3)</span>
                  </CardTitle>
                  <p className="text-sm text-gray-600"></p>
                </CardHeader>
                <CardContent>
                  {reviewLoading ? (
                    <div className="flex justify-center py-8">
                      <div className="animate-spin w-6 h-6 border-2 border-orange-500 border-t-transparent rounded-full"></div>
                    </div>
                  ) : retryList.length === 0 ? (
                    <div className="text-center py-8 text-gray-500">
                      <div className="text-4xl mb-2"></div>
                      <p></p>
                      <p className="text-sm"></p>
                    </div>
                  ) : (
                    <div className="space-y-2">
                      {retryList.slice(0, 10).map((item, index) => (
                        <div 
                          key={item.id || index} 
                          className="bg-orange-50 border border-orange-200 rounded-lg p-3 hover:bg-orange-100 cursor-pointer transition-colors"
                          onClick={() => onStartTraining(item.japaneseSentence)}
                        >
                          <div className="text-gray-800 font-medium">{item.japaneseSentence}</div>
                        </div>
                      ))}
                    </div>
                  )}
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Bookmarks Tab */}
          <TabsContent value="bookmarks">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center space-x-2">
                  <span></span>
                  <span></span>
                </CardTitle>
                <p className="text-sm text-gray-600"></p>
              </CardHeader>
              <CardContent>
                {bookmarks.length === 0 ? (
                  <div className="h-64 flex flex-col items-center justify-center text-gray-500">
                    <div className="w-16 h-16 bg-gray-200 rounded-full flex items-center justify-center mb-4">
                      <span></span>
                    </div>
                    <p className="font-medium"></p>
                    <p className="text-sm"></p>
                  </div>
                ) : (
                  <div className="space-y-3">
                    <div className="text-sm text-gray-600 mb-4">
                      : {bookmarks.length}
                    </div>
                    {bookmarks.map((bookmark, index) => {
                      const [problem, number] = bookmark.split('_');
                      return (
                        <div key={index} className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 hover:bg-yellow-100 transition-colors">
                          <div className="flex items-start justify-between">
                            <div 
                              className="flex-1 cursor-pointer" 
                              onClick={() => onStartTraining(problem)}
                            >
                              <div className="flex items-center space-x-2 mb-2">
                                <span className="text-yellow-500"></span>
                                <span className="text-sm font-medium text-gray-700"> {number || index + 1}</span>
                                <span className="text-xs text-blue-600 bg-blue-100 px-2 py-1 rounded"></span>
                              </div>
                              <div className="text-gray-800 font-medium hover:text-blue-600 transition-colors">{problem}</div>
                              <div className="text-xs text-gray-500 mt-1"></div>
                            </div>
                            <button 
                              onClick={(e) => {
                                e.stopPropagation();
                                removeBookmark(bookmark);
                              }}
                              className="text-gray-400 hover:text-red-500 transition-colors ml-2"
                              title=""
                            >
                              
                            </button>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>



          {/* Simulation Tab */}
          <TabsContent value="simulation">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center space-x-2">
                  <span></span>
                  <span></span>
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-6 text-center">
                  <p className="font-medium text-yellow-800 mb-2"></p>
                  <p className="text-sm text-yellow-700"></p>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Account Tab */}
          <TabsContent value="account">
            <div className="space-y-6">
              {/* Current Plan */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center space-x-2">
                    <span></span>
                    <span></span>
                  </CardTitle>
                  <p className="text-sm text-gray-600"></p>
                </CardHeader>
                <CardContent>
                  <div className="flex justify-between items-center p-4 bg-gray-50 rounded-lg mb-4">
                    <div>
                      <p className="font-medium text-gray-900"></p>
                      <p className="text-sm text-gray-600"> 50/</p>
                    </div>
                    <Badge></Badge>
                  </div>
                </CardContent>
              </Card>

              {/* Plan Upgrade */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center space-x-2">
                    <span></span>
                    <span></span>
                  </CardTitle>

                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="border border-gray-200 rounded-lg p-4">
                    <div className="flex justify-between items-center mb-2">
                      <span className="font-medium"></span>
                      <Button variant="outline" size="sm"></Button>
                    </div>
                  </div>
                  
                  <div className="border border-gray-200 rounded-lg p-4">
                    <div className="flex justify-between items-center mb-2">
                      <span className="font-medium"></span>
                      <Button variant="outline" size="sm"></Button>
                    </div>
                    <p className="text-sm text-gray-600">122</p>
                  </div>
                </CardContent>
              </Card>

              {/* Plan Comparison */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center space-x-2">
                    <span></span>
                    <span></span>
                  </CardTitle>
                  <p className="text-sm text-gray-600"></p>
                </CardHeader>
                <CardContent>
                  <div className="overflow-x-auto">
                    <table className="w-full text-sm">
                      <thead>
                        <tr className="bg-orange-50 border-b">
                          <th className="text-left py-2 font-bold"></th>
                          <th className="text-center py-2 font-bold"></th>
                          <th className="text-center py-2 font-bold"></th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr className="bg-gray-50 border-b">
                          <td className="py-2"></td>
                          <td className="text-center">980</td>
                          <td className="text-center">1,300</td>
                        </tr>
                        <tr className="bg-white border-b">
                          <td className="py-2">2</td>
                          <td className="text-center">9,800</td>
                          <td className="text-center">13,000</td>
                        </tr>
                        <tr className="bg-gray-50 border-b">
                          <td className="py-2">1</td>
                          <td className="text-center">50</td>
                          <td className="text-center">100</td>
                        </tr>
                        <tr className="bg-white border-b">
                          <td className="py-2"></td>
                          <td className="text-center"></td>
                          <td className="text-center"></td>
                        </tr>
                        <tr className="bg-gray-50 border-b">
                          <td className="py-2"></td>
                          <td className="text-center"></td>
                          <td className="text-center"></td>
                        </tr>
                        <tr className="bg-white border-b">
                          <td className="py-2"></td>
                          <td className="text-center"></td>
                          <td className="text-center"></td>
                        </tr>
                        <tr className="bg-gray-50 border-b">
                          <td className="py-2"></td>
                          <td className="text-center"></td>
                          <td className="text-center"></td>
                        </tr>
                        <tr className="bg-white border-b">
                          <td className="py-2"></td>
                          <td className="text-center"></td>
                          <td className="text-center"></td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </CardContent>
              </Card>

              {/* Payment Management */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center space-x-2">
                    <span></span>
                    <span></span>
                  </CardTitle>
                  <p className="text-sm text-gray-600"></p>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="flex justify-between items-center">
                    <span></span>
                    <span>{planInfo.nextBilling}</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span></span>
                    <span>1,300</span>
                  </div>
                  <div className="flex space-x-2">
                    <Button variant="outline" size="sm" className="flex-1">
                       
                    </Button>
                    <Button variant="outline" size="sm" className="flex-1">
                       
                    </Button>
                  </div>
                </CardContent>
              </Card>

              {/* Cancellation */}
              <Card>
                <CardContent className="p-6">
                  <Button 
                    onClick={cancelSubscription}
                    className="w-full bg-red-500 hover:bg-red-600 text-white"
                  >
                    
                  </Button>
                  <p className="text-xs text-center text-gray-500 mt-2">
                    
                  </p>
                </CardContent>
              </Card>

              {/* Account Info */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center space-x-2">
                    <span></span>
                    <span></span>
                  </CardTitle>
                  <p className="text-sm text-gray-600"></p>
                </CardHeader>
                <CardContent>
                  <div className="flex items-center space-x-3 p-4 bg-gray-50 rounded-lg">
                    <div className="w-10 h-10 bg-blue-500 text-white rounded-full flex items-center justify-center">
                      <span>S</span>
                    </div>
                    <div className="flex-1">
                      <p className="font-medium">{userAccount.email}</p>
                      <p className="text-sm text-gray-600">{userAccount.registeredAt}</p>
                    </div>
                    <Button variant="outline" size="sm"></Button>
                  </div>
                  
                  <div className="mt-4 pt-4 border-t text-center">
                    <Button 
                      variant="outline" 
                      className="text-red-600 border-red-600 hover:bg-red-50"
                      onClick={() => {
                        // Clear any stored data
                        localStorage.clear();
                        sessionStorage.clear();
                        
                        // If onShowAuth is provided, show auth screen
                        if (onShowAuth) {
                          onShowAuth();
                        } else {
                          // Fallback: reload page
                          window.location.reload();
                        }
                      }}
                    >
                      
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}