import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from './components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './components/ui/tabs';
import { Button } from './components/ui/button';
import { Badge } from './components/ui/badge';
// import { apiRequest } from '@/lib/queryClient';

interface UsageStats {
  todayUsers: number;
  todayProblems: number;
  categoryStats: Record<string, number>;
  realtimeConnections?: number;
}

interface SubscriptionInfo {
  activeSubscribers: number;
  trialUsers: number;
  monthlyRevenue: number;
}

interface User {
  id: string;
  email: string;
  createdAt: string;
  subscriptionStatus: 'active' | 'trial' | 'inactive';
  lastActive?: string;
}

interface SystemStatus {
  claudeApi: 'healthy' | 'degraded' | 'down';
  stripeApi: 'healthy' | 'degraded' | 'down';
  database: 'healthy' | 'degraded' | 'down';
  lastUpdated: string;
}

export default function AdminDashboard({ onBackToMenu }: { onBackToMenu: () => void }) {
  // Record current page as admin dashboard for refresh persistence
  useEffect(() => {
    localStorage.setItem('englishTrainingCurrentPage', 'admin');
  }, []);

  const [usageStats, setUsageStats] = useState<UsageStats>({
    todayUsers: 0,
    todayProblems: 0,
    categoryStats: {}
  });
  const [subscriptionInfo, setSubscriptionInfo] = useState<SubscriptionInfo>({
    activeSubscribers: 0,
    trialUsers: 0,
    monthlyRevenue: 0
  });
  const [users, setUsers] = useState<User[]>([]);
  const [systemStatus, setSystemStatus] = useState<SystemStatus>({
    claudeApi: 'healthy',
    stripeApi: 'healthy',
    database: 'healthy',
    lastUpdated: new Date().toLocaleString()
  });
  const [notification, setNotification] = useState('');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    loadDashboardData();
    // Refresh data every 30 seconds
    const interval = setInterval(loadDashboardData, 30000);
    return () => clearInterval(interval);
  }, []);

  const loadDashboardData = async () => {
    try {
      setIsLoading(true);
      
      // Mock data for testing
      setUsageStats({
        todayUsers: 156,
        todayProblems: 1240,
        categoryStats: {
          'TOEIC': 345,
          '': 298,
          '': 267,
          '': 198,
          '': 132
        },
        realtimeConnections: 23
      });

      setSubscriptionInfo({
        activeSubscribers: 89,
        trialUsers: 34,
        monthlyRevenue: 45600
      });

      // Mock users data
      setUsers([
        { id: '1', email: 'user1@example.com', createdAt: '2025-08-01', subscriptionStatus: 'active', lastActive: '2025-08-07' },
        { id: '2', email: 'user2@example.com', createdAt: '2025-07-28', subscriptionStatus: 'trial', lastActive: '2025-08-06' },
        { id: '3', email: 'user3@example.com', createdAt: '2025-07-25', subscriptionStatus: 'inactive', lastActive: '2025-08-05' },
      ]);

      // Mock system status
      setSystemStatus({
        claudeApi: 'healthy',
        stripeApi: 'healthy', 
        database: 'healthy',
        lastUpdated: new Date().toLocaleString()
      });

    } catch (error) {
      console.error('Failed to load dashboard data:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const sendNotification = async () => {
    if (!notification.trim()) return;

    try {
      // Mock notification sending
      console.log('Sending notification:', notification);
      alert('');
      setNotification('');
    } catch (error) {
      console.error('Failed to send notification:', error);
      alert('');
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'healthy': return 'bg-green-100 text-green-800';
      case 'degraded': return 'bg-yellow-100 text-yellow-800';
      case 'down': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'healthy': return '';
      case 'degraded': return '';
      case 'down': return '';
      default: return '';
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="animate-spin w-8 h-8 border-4 border-blue-500 border-t-transparent rounded-full"></div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b px-4 py-3">
        <div className="flex items-center justify-between">
          <Button 
            variant="ghost"
            onClick={onBackToMenu}
            className="text-gray-600 hover:text-gray-800"
          >
             
          </Button>
          <h1 className="text-xl font-semibold text-gray-900"></h1>
          <div className="text-sm text-gray-500">
            : {systemStatus.lastUpdated}
          </div>
        </div>
      </div>

      <div className="p-6">
        <Tabs defaultValue="usage" className="space-y-6">
          <TabsList className="grid w-full grid-cols-5">
            <TabsTrigger value="usage"></TabsTrigger>
            <TabsTrigger value="problems"></TabsTrigger>
            <TabsTrigger value="subscription"></TabsTrigger>
            <TabsTrigger value="users"></TabsTrigger>
            <TabsTrigger value="admin"></TabsTrigger>
          </TabsList>

          {/*  */}
          <TabsContent value="usage" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium"></CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold text-blue-600">
                    {usageStats.todayUsers.toLocaleString()}
                  </div>
                  <p className="text-xs text-gray-500 mt-1"></p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium"></CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold text-green-600">
                    {usageStats.todayProblems.toLocaleString()}
                  </div>
                  <p className="text-xs text-gray-500 mt-1"></p>
                </CardContent>
              </Card>

              {usageStats.realtimeConnections !== undefined && (
                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle className="text-sm font-medium"></CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="text-2xl font-bold text-orange-600">
                      {usageStats.realtimeConnections}
                    </div>
                    <p className="text-xs text-gray-500 mt-1"></p>
                  </CardContent>
                </Card>
              )}
            </div>

            {/*  */}
            <Card>
              <CardHeader>
                <CardTitle></CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {Object.entries(usageStats.categoryStats).map(([category, count]) => (
                    <div key={category} className="flex items-center justify-between">
                      <span className="text-sm font-medium">{category}</span>
                      <div className="flex items-center space-x-2">
                        <div className="w-32 bg-gray-200 rounded-full h-2">
                          <div 
                            className="bg-blue-500 h-2 rounded-full" 
                            style={{ 
                              width: `${Math.min(100, (count / Math.max(...Object.values(usageStats.categoryStats))) * 100)}%` 
                            }}
                          />
                        </div>
                        <span className="text-sm text-gray-600 w-12 text-right">{count}</span>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/*  */}
          <TabsContent value="problems" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>7</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {Object.entries(usageStats.categoryStats)
                    .sort(([,a], [,b]) => b - a)
                    .map(([category, count], index) => (
                    <div key={category} className="flex items-center space-x-4">
                      <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center text-sm font-bold text-blue-600">
                        {index + 1}
                      </div>
                      <div className="flex-1">
                        <div className="font-medium">{category}</div>
                        <div className="text-sm text-gray-500">{count} </div>
                      </div>
                      <Badge variant="secondary">{count}</Badge>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/*  */}
          <TabsContent value="subscription" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium"></CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold text-green-600">
                    {subscriptionInfo.activeSubscribers.toLocaleString()}
                  </div>
                  <p className="text-xs text-gray-500 mt-1"></p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium"></CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold text-orange-600">
                    {subscriptionInfo.trialUsers.toLocaleString()}
                  </div>
                  <p className="text-xs text-gray-500 mt-1"></p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium"></CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold text-blue-600">
                    {subscriptionInfo.monthlyRevenue.toLocaleString()}
                  </div>
                  <p className="text-xs text-gray-500 mt-1">Stripe</p>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/*  */}
          <TabsContent value="users" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle></CardTitle>
              </CardHeader>
              <CardContent>
                <div className="overflow-x-auto">
                  <table className="w-full text-sm">
                    <thead>
                      <tr className="border-b">
                        <th className="text-left py-2"></th>
                        <th className="text-left py-2"></th>
                        <th className="text-left py-2"></th>
                        <th className="text-left py-2"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {users.slice(0, 20).map((user) => (
                        <tr key={user.id} className="border-b">
                          <td className="py-2">{user.email}</td>
                          <td className="py-2">{new Date(user.createdAt).toLocaleDateString()}</td>
                          <td className="py-2">
                            <Badge 
                              variant={
                                user.subscriptionStatus === 'active' ? 'default' :
                                user.subscriptionStatus === 'trial' ? 'secondary' : 'outline'
                              }
                            >
                              {user.subscriptionStatus === 'active' ? '' :
                               user.subscriptionStatus === 'trial' ? '' : ''}
                            </Badge>
                          </td>
                          <td className="py-2">
                            {user.lastActive ? new Date(user.lastActive).toLocaleDateString() : '-'}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/*  */}
          <TabsContent value="admin" className="space-y-6">
            {/*  */}
            <Card>
              <CardHeader>
                <CardTitle></CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <span className="font-medium">Claude API</span>
                    <Badge className={getStatusColor(systemStatus.claudeApi)}>
                      {getStatusText(systemStatus.claudeApi)}
                    </Badge>
                  </div>
                  <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <span className="font-medium">Stripe API</span>
                    <Badge className={getStatusColor(systemStatus.stripeApi)}>
                      {getStatusText(systemStatus.stripeApi)}
                    </Badge>
                  </div>
                  <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <span className="font-medium"></span>
                    <Badge className={getStatusColor(systemStatus.database)}>
                      {getStatusText(systemStatus.database)}
                    </Badge>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/*  */}
            <Card>
              <CardHeader>
                <CardTitle></CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <textarea
                  value={notification}
                  onChange={(e) => setNotification(e.target.value)}
                  placeholder="..."
                  className="w-full h-24 p-3 border border-gray-300 rounded-lg resize-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
                <Button 
                  onClick={sendNotification}
                  disabled={!notification.trim()}
                  className="w-full"
                >
                  
                </Button>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}